<template>
    <div class="container">
        <img class="guitar-logo" src="../assets/page-logo.svg"/>
        <svg class="guitar_pick" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 794 877">
            <g data-name="Layer 2">
                <g data-name="q1 and 4">
                <image width="794" height="877" xlink:href="data:image/png;base64," style="mix-blend-mode:multiply" opacity=".3"/>
                <path d="M705.11 465.36c-20.81 56.93-44.91 113.18-69.94 167.22-25.61 55.27-59.2 112.7-102.77 156-9.93 9.87-19.36 20.25-30.93 28.56-10.83 7.77-22 14.33-34.57 18.36-10.78 3.45-21.81 3.67-32.89 1.81-59.75-10-114.86-62.08-156-103.42-69.28-69.59-126.07-152.2-175.32-236.83-8.18-14-16-28.26-24-42.4-47.25-83.74-88.91-175.84-47.05-270.56C73.15 90.22 186.06 52.58 279.39 34.93 306 29.88 332.86 26 359.79 23 466 11.13 586.65 1.29 676 71.63a191.58 191.58 0 0 1 46.66 53.44c45.89 77.71 36.4 167.08 12.39 249.53-8.85 30.28-18.93 60.61-29.94 90.76z" fill="#33b5b4" fill-rule="evenodd"/>
                </g>
            </g>
        </svg>
        <div class="question-content">
            <h1 class="question-number">Question 3 of 6</h1>
            <h2 class="question">Your friend has a spare ticket to a gig.<br/>Who do you hope is playing?</h2>
            <ul class="answer-container">
                <li class="answer"><router-link to="question-four"><a @click="addToWorkout">Beyonc</a></router-link></li>
                <li class="answer"><router-link to="question-four"><a @click="addToLegends">Rolling Stones</a></router-link></li>
                <li class="answer"><router-link to="question-four"><a @click="addToElectronica">Daft Punk</a></router-link></li>
                <li class="answer"><router-link to="question-four"><a @click="addToCampfire">Ed Sheeran</a></router-link></li>
                <li class="answer"><router-link to="question-four"><a @click="addToChill">Adele</a></router-link></li>
                <li class="answer"><router-link to="question-four"><a @click="addToAcoustic">Lorde</a></router-link></li>
            </ul>
        </div>
    <img class="pick-image campfire" src="../assets/campfirenew.png"/>
    <img class="pick-image legend" src="../assets/legendnew.png"/>
    <img class="pick-image acoustic" src="../assets/acousticboombox.png"/>
    <button class="restart" @click="restart">Start Again</button>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex'
    export default {
        methods: {
            ...mapMutations([
            'addToLegends','addToWorkout','addToCampfire','addToElectronica','addToAcoustic','addToChill'
            ]),
             restart() {
                this.$router.push({path: '/'})
                store.replaceState({})
             }
        }
    }
</script>

<style scoped>

/* minus the height and width so it's centered */
.question-content {
    left: calc(50% - 360px);
    top: calc(50% - 384px);
    transform: translate(calc(-50% + 360px),calc(-50% + 384px));
}

.guitar_pick path {
    fill: #46166B;
}

.campfire {
    width: 150px;
    height: auto;
    bottom: 5%;
    left: 55%;
    animation: fade-in .75s ease-out .75s forwards;
    opacity: 0;
}


.legend {
    display: none;
    opacity: 0;
}

.acoustic{
    display: none;
    opacity: 0;
}

@media(min-width: 769px) {
    .legend {
        display: block;
        width: 150px;
        height: auto;
        left: 15%;
        bottom: 20%;
        animation: fade-in .75s ease-out 1s forwards;
    }

    .acoustic {
        display: block;
        width: 150px;
        height: auto;
        right: 5%;
        animation: fade-in .75s ease-out 1s forwards;
    }
}

@media(min-width: 1000px) {
    .legend {
        left: 20%;
        width: 190px;
    }

    .acoustic {
        right: 18%;
        width: 115px;
        height: auto;
    }
}

</style>


