<template>
    <div class="container">
        <img class="guitar-logo" src="../assets/page-logo.svg"/>
        <svg class="guitar_pick" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 794 877">
            <g data-name="Layer 2">
                <g data-name="q1 and 4">
                <image width="794" height="877" xlink:href="data:image/png;base64," style="mix-blend-mode:multiply" opacity=".3"/>
                <path d="M705.11 465.36c-20.81 56.93-44.91 113.18-69.94 167.22-25.61 55.27-59.2 112.7-102.77 156-9.93 9.87-19.36 20.25-30.93 28.56-10.83 7.77-22 14.33-34.57 18.36-10.78 3.45-21.81 3.67-32.89 1.81-59.75-10-114.86-62.08-156-103.42-69.28-69.59-126.07-152.2-175.32-236.83-8.18-14-16-28.26-24-42.4-47.25-83.74-88.91-175.84-47.05-270.56C73.15 90.22 186.06 52.58 279.39 34.93 306 29.88 332.86 26 359.79 23 466 11.13 586.65 1.29 676 71.63a191.58 191.58 0 0 1 46.66 53.44c45.89 77.71 36.4 167.08 12.39 249.53-8.85 30.28-18.93 60.61-29.94 90.76z" fill="#33b5b4" fill-rule="evenodd"/>
                </g>
            </g>
        </svg>
        <div class="question-content">
            <h1 class="question-number">Question 6 of 6</h1>
            <h2 class="question">Youve got an important deadline coming<br> up in work. What do you do?</h2>
            <ul class="answer-container">
                <li class="answer"><router-link to="result"><a @click="addToLegends">Clear your schedule and focus all of your attention on that one thing</a></router-link></li>
                <li class="answer"><router-link to="result"><a @click="addToAcoustic">Leave it until the last minute and then panic</a></router-link></li>
                <li class="answer"><router-link to="result"><a @click="addToCampfire">Ask your colleagues to help you</a></router-link></li>
                <li class="answer"><router-link to="result"><a @click="addToWorkout">Write up a colour coded, bullet pointed plan on how you can hit the deadline and achieve all your other work priorities</a></router-link></li>
                <li class="answer"><router-link to="result"><a @click="addToElectronica">Go out the night before, telling yourself youll get up early in the morning to finish off the project</a></router-link></li>
                <li class="answer"><router-link to="result"><a @click="addToChill">Delegate the project to juniors,telling them youll supervise</a></router-link></li>
            </ul>
        </div>
         <img class="pick-image campfire" src="../assets/campfirenew.png"/>
         <img class="pick-image legend" src="../assets/legendnew.png"/>
         <img class="pick-image acoustic" src="../assets/acousticboombox.png"/>
        <button class="restart" @click="restart">Start Again</button>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex'
    export default {
        methods: {
            ...mapMutations([
            'addToLegends','addToWorkout','addToCampfire','addToElectronica','addToAcoustic','addToChill'
            ]),
             restart() {
                this.$router.push({path: '/'})
                store.replaceState({})
             }
        }
    }
</script>

<style scoped>
.guitar_pick path {
    fill: #46166B;
}

/* minus the height and width so it's centered */
.question-content {
    left: calc(50% - 961px);
    top: calc(50% - 450px);
    transform: translate(calc(-50% + 961px),calc(-50% + 450px));
    margin-top: -2em;
}

.answer a {
    font-size: .75rem;
}

.campfire {
    width: 125px;
    height: auto;
    bottom: -2%;
    left: 45%;
    opacity: 0;
    animation: fade-in .75s ease-out .75s forwards;
}

.legend,.acoustic {
    display: none;
    opacity: 0;
}

@media(min-width: 769px) {
    .legend {
        display: block;
        width: 175px;
        height: auto;
        left: 9%;
        bottom: 10%;
        animation: fade-in .75s ease-out 1s forwards;
    }

    .acoustic {
        display: block;
        width: 175px;
        height: auto;
        right: 10%;
        bottom: 5%;
        animation: fade-in .75s ease-out 1s forwards;
    }
}

@media(min-width: 1000px) {
    .legend {
        left: 18%;
    }

    .acoustic {
        right: 18%;
    }
}

</style>


